import $ from 'expose?$!expose?jQuery!jquery';
import Mustache from 'mustache';
import ProgressBar from 'progressbar.js';
import 'fullpage.js';
import 'jquery-youtube-background';
import './lib/bootstrap-filestyle.min';
import './data/data.js';

var global = {};
function fullPageInit() {
    $('#fullpage').fullpage({
        navigation: true,
        autoScrolling: true,
        loopHorizontal: true,
        onLeave: function (index, nextIndex, direction) {
            console.log(index, nextIndex, direction);
            if (nextIndex == 3) {
                setTimeout(function () {
                    $(window).trigger('progressBarRun');
                }, 500);
            }
        },
        afterRender: function () {
        }
    });
}

function progressBarInit() {
    var $progressBars = $('.skills .skill .skill__progress-bar'),
        initProgressBar = [];

    $progressBars.each(function (index) {
        var self = this;
        initProgressBar[index] = {};
        initProgressBar[index].init = new ProgressBar.Line('#' + $(this).attr('id'),
            {
                strokeWidth: 4,
                easing: 'easeInOut',
                duration: 1400,
                color: $(self).attr('data-color'),
                trailColor: '#eee',
                trailWidth: 1,
                svgStyle: {width: '100%', height: '4px'},
                text: {
                    style: {
                        color: '#f6f6f6',
                        position: 'absolute',
                        right: '0',
                        top: '2px',
                        padding: 0,
                        margin: 0,
                        transform: null
                    },
                    autoStyleContainer: false
                },
                from: {color: $(self).attr('data-color')},
                to: {color: $(self).attr('data-color')},
                step: function (state, bar) {
                    bar.setText(Math.round(bar.value() * 100) + '%');
                }
            });
        initProgressBar[index]['count'] = $(self).attr('data-count') / 100;
    });

    var run = function () {
        initProgressBar.map(function (skill) {
            skill.init.animate(skill.count);
        });
    };
    $(window).on('progressBarRun', function () {
        run();
    });
}

function loadUser() {
    $.get('./temp/fullpage.mst', function (template) {
        var rendered = '';
        data.map(function (persone) {
            rendered += Mustache.render(template, persone);
        });
        $('#fullpage').html(rendered);
    }).done(function () {
        fullPageInit();
        progressBarInit()
    });
}
$(function () {
    loadUser();
});


