import skill from 'raw-loader!../../tpl/skill.tpl';
import Mustache from 'mustache';
const skills = [
    {
        name: 'html',
        count: '90',
        color: '#e52'
    },
    {
        name: 'css3',
        count: '90',
        color: '#17b'
    },
    {
        name: 'js',
        count: '70',
        color: '#fe2'
    },
    {
        name: 'jquery',
        count: '75',
        color: '#f80'
    },
    {
        name: 'scss',
        count: '80',
        color: '#c6538c'
    },
    {
        name: 'php',
        count: '40',
        color: '#68c'
    },
    {
        name: 'reactjs',
        count: '40',
        color: '#61dafb'
    },
    {
        name: 'bootstrap',
        count: '80',
        color: '#6f5499'
    },
    {
        name: 'prestashop',
        count: '70',
        color: '#df0067'
    },
    {
        name: 'git',
        count: '75',
        color: '#f14e32'
    },
    {
        name: 'webpack',
        count: '50',
        color: '#68c'
    },
    {
        name: 'gulp',
        count: '40',
        color: '#CF4647'
    }
];
function getSkills() {
    let rendered = '<div class="block"><h2 class="primary-title text-center">Skills</h2><div class="skills">';
    skills.map(function(index){
        rendered += Mustache.render(skill, index);
    });
    rendered += "</div></div>";
    return rendered;
}

module.exports = getSkills();





